import { Field } from '@nestjs/graphql';
import { InputType } from '@nestjs/graphql';
import { HideField } from '@nestjs/graphql';

@InputType()
export class ChallengeSlugDomainIdCompoundUniqueInput {

    @Field(() => String, {nullable:false})
    slug!: string;

    @HideField()
    domainId!: number;
}
